.footer {

    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    text-align: center;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(80, 80, 80)), to(rgb(0, 0, 0)));
    background-image: -webkit-linear-gradient(rgb(80, 80, 80), rgb(0, 0, 0));
    background-image: -o-linear-gradient(rgb(80, 80, 80), rgb(0, 0, 0));
    background-image: linear-gradient(rgb(80, 80, 80), rgb(0, 0, 0));
}

.footer p {
    font-size: 0.8rem;
    width: 75%;
    margin: auto;
}


