@font-face {
    font-family: magic;
    src: url(../../assets/fonts/MagicMedieval.ttf);
}

button {
    background-image: -webkit-gradient(linear, left top, left bottom, from(maroon), to(rgb(65, 0, 0)));
    background-image: -webkit-linear-gradient(maroon, rgb(65, 0, 0));
    background-image: -o-linear-gradient(maroon, rgb(65, 0, 0));
    background-image: linear-gradient(maroon, rgb(65, 0, 0));
    color: white;
    border-radius: 1rem;
    padding: 1rem 1rem 0rem 1rem;
    line-height: 1rem;
    font-family: magic;
    font-size: 1.2rem;
    margin: 0rem 0.5rem;
}

button:hover {
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(255, 255, 255)), to(rgb(61, 61, 61)));
    background-image: -webkit-linear-gradient(rgb(255, 255, 255), rgb(61, 61, 61));
    background-image: -o-linear-gradient(rgb(255, 255, 255), rgb(61, 61, 61));
    background-image: linear-gradient(rgb(255, 255, 255), rgb(61, 61, 61));
    color: maroon;
}

button:focus {
    outline: none;
}


